import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import SpotlightSearch from "./spotlight-search/App";
import FormikTodo from "./formik-todo/App";
import DarkThemeToggle from "./darktheme-toggle/App";
import RandomUserTable from "./randomuserapi-table/App";

ReactDOM.render(
  <React.StrictMode>
    <Router>
      <Switch>
        <Route path="/spotlight-search" component={SpotlightSearch} />
        <Route path="/formik-todo" component={FormikTodo} />
        <Route path="/darktheme-toggle" component={DarkThemeToggle} />
        <Route path="/randomuserapi-table" component={RandomUserTable} />
      </Switch>
    </Router>
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
